html {
  box-sizing: border-box;
  scroll-behavior: smooth; }
  @media (max-width: 640px) {
    html {
      font-size: 10px; } }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: black;
  background: white;
  font-family: 'Poppins', sans-serif;
  border: 10px solid #171F3F; }
  @media (max-width: 768px) {
    body {
      border: none; } }

.card {
  background: #FFFFFF;
  max-width: 450px;
  box-shadow: 0px 0.4rem 1rem rgba(24, 41, 67, 0.09);
  border-radius: 0, 5rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .card__background {
    background: #F5F7FA;
    color: #0593FB;
    padding: 1rem 3rem;
    text-align: right; }
  .card__box {
    padding: 1.5rem; }
  .card__title {
    font-style: normal;
    font-weight: 600;
    font-size: 0.75rem;
    line-height: 2rem; }
  .card__paragraph {
    font-style: normal;
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 24px; }
  .card__link {
    color: #0593FB;
    line-height: 2rem;
    height: 2rem;
    display: inline-flex;
    align-items: center;
    font-style: normal;
    font-weight: 250;
    font-size: 14px;
    line-height: 2rem; }
    .card__link::before {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
      margin-right: 0.5rem; }
    .card__link + .card__link {
      padding-left: 1.5rem; }
      .card__link + .card__link::after {
        content: '';
        height: 2rem;
        width: 2rem;
        background: #CBD2D9;
        display: block;
        position: absolute; }
      .card__link + .card__link--code ::before {
        content: '';
        display: block;
        width: 24px;
        height: 24px;
        margin-right: 0.5rem; }

.card-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  list-style-type: none;
  column-gap: 24px;
  grid-gap: 30px;
  padding-inline-start: 0; }
  @media (max-width: 640px) {
    .card-list {
      grid-template-columns: 1fr; } }

.footer {
